.cs-ac-status-verified::before,
.cs-ac-status-not-verified::before {
    content: '';
    width: 14px;
    height: 14px;
    margin-right: 6px;
    display: inline-block;
    background-repeat: no-repeat;
    background-size: contain;
    vertical-align: middle;
}

.cs-ac-status-verified::before {
    background-image: url('../../../assets/images/overview/verified.png');
}

.cs-ac-status-not-verified::before {
    background-image: url('../../../assets/images/overview/not-verified.png');
}