.cs-verify-account > *:not(:first-child) {
    margin-left: 20px;
}

.cs-verify-account > a {
    text-decoration: underline;
    color: inherit;
}

.cs-verify-account .cs-verified::before,
.cs-verify-account .cs-not-verified::before {
    content: '';
    width: 14px;
    height: 14px;
    margin-right: 6px;
    display: inline-block;
    background-repeat: no-repeat;
    background-size: contain;
    vertical-align: middle;
}

.cs-verify-account .cs-verified::before {
    background-image: url(/static/media/verified.966edf2d.png);
}

.cs-verify-account .cs-not-verified::before {
    background-image: url(/static/media/not-verified.492948ab.png);
}

.acc_balance .card-body > .d-flex > div:last-child * {
    text-align: right;
}

.cs-status-completed::before,
.cs-status-failed::before,
.cs-status-warning::before {
    content: '';
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin-right: 10px;
}

.cs-status-completed::before {
    background-color: green;
}

.cs-status-failed::before {
    background-color: red;
}

.cs-status-warning::before {
    background-color: orange;
}
