.card .nice-select {
    min-width: 150px;
}

.cs-status-completed::before,
.cs-status-failed::before,
.cs-status-warning::before {
    content: '';
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin-right: 10px;
}

.cs-status-completed::before {
    background-color: green;
}

.cs-status-failed::before {
    background-color: red;
}

.cs-status-warning::before {
    background-color: orange;
}